.navigator {
  position: relative;
  margin: auto;
  overflow: auto;
}
.navigator ul li {
    list-style: none;
    float: left;
    line-height: 45px;
    font-weight: 600;
    font-size: 11pt;
}
.navigator ul li:hover {
      cursor: pointer;
}
.navigator.nav-ctn-center ul li {
    text-align: center;
}
.navigator.nav-center ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.navigator.nav-center ul li {
      width: calc(100% - 30px);
      padding: 0 15px;
      margin: auto;
}
.navigator.nav-all ul li {
    padding: 0 15px;
}
.navigator.nav-full ul li {
    width: calc(100% - 30px);
